	import {  
        mapMutations  
    } from 'vuex';
	export default{
		data(){
			return {
				mobile: '',
				password: '',
				logining: false
			}
		},
		onLoad(){
			//uni.hideTabBar();
		},
		methods: {
			...mapMutations(['setToken','setUserInfo']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			}, 
			async toLogin(){
				let _this = this;
				_this.logining = true;
				const {username, password} = this;
				
				const sendData = {
					username,
					password
				};
				_this.$Request.post('login',{username:username,password:password,client:'wap'}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.setToken(res.datas.token);
						_this.setUserInfo(res.datas);
						uni.switchTab({
							url:"/pages/member/home", 
							success (res) {
									const currentPages = getCurrentPages()  
									console.log(currentPages[0]);  
									currentPages[0].onLoad({  
										...currentPages[0].options  
									})
									/* let page = getCurrentPages().pop(); 
									if (page == undefined || page == null) { 
									  return ;
									 } 
									 page.onLoad() ; */
							}
						}) 
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						_this.logining = false;
					}
				});
		
			},
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			toBack: function() {
				uni.switchTab({
					url:'/pages/index/index',
				})
			}
		},

	}
